import React, { Component } from 'react';
import OpenSeadragon from 'openseadragon';
import logo from './dzi_files/10/0_0.jpg';
//import logo1 from './dzi_files';

export default class Image extends Component {

    constructor(props){
        super(props);
        var imagePath = ''  
    }

componentDidMount() {
    
    var dimage = {
        Image: {
        xmlns: "http://schemas.microsoft.com/deepzoom/2008",
        Url: '//openseadragon.github.io/example-images/duomo/duomo_files/',
        Format: "jpg",
        TileSize: "256",
        Size: {
            Width:  "13920",
            Height: "10200"
        }
        }
    };

    var dimage1 = {
        Image: {
        xmlns: "http://schemas.microsoft.com/deepzoom/2008",
        Url: ' https://github.com/preetham1230/Images/blob/master/dzi_files/',
        Format: "jpg",
        TileSize: "256",
        CrossOriginPolicy: '*',
        Size: {
            Width:  "13920",
            Height: "10200"
        }
        }
    };
    
    var viewer = OpenSeadragon({
        id: "image-viewer",
        prefixUrl: "//openseadragon.github.io/openseadragon/images/",
        sequenceMode: true,
        tileSources: [dimage,dimage1]
    });
}
    

    render() {
        
        
  
        return (
            <div>
            <div id="image-viewer" style={{width: '800px', height: '800px', padding:'50px'}}  ></div>

            <div>
                Image
                <img src={logo}></img>
            </div>

            </div>
        )
    }
}



